<template>
  <div>
    <div style="display: flex;justify-content: space-between">
      <el-button icon="el-icon-plus" type="primary" @click="showAddSalaryView"></el-button>
      <el-button icon="el-icon-refresh" type="success" @click="initSalaries"></el-button>
    </div>
    <div style="margin-top: 10px">
      <el-table :data="salaries" border stripe>
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column width="120" prop="name" label=""></el-table-column>
        <el-table-column width="70" prop="basicSalary" label=""></el-table-column>
        <el-table-column width="70" prop="trafficSalary" label=""></el-table-column>
        <el-table-column width="70" prop="lunchSalary" label=""></el-table-column>
        <el-table-column width="70" prop="bonus" label=""></el-table-column>
        <el-table-column width="100" prop="createDate" label=""></el-table-column>
        <el-table-column label="" align="center">
          <el-table-column width="70" prop="pensionPer" label=""></el-table-column>
          <el-table-column width="70" prop="pensionBase" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="" align="center">
          <el-table-column width="70" prop="medicalPer" label=""></el-table-column>
          <el-table-column width="70" prop="medicalBase" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="" align="center">
          <el-table-column width="70" prop="unemployBasePer" label=""></el-table-column>
          <el-table-column width="70" prop="unemployBase" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="" align="center">
          <el-table-column width="70" prop="jobInjuryPer" label=""></el-table-column>
          <el-table-column width="70" prop="jobInjuryBase" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="" align="center">
          <el-table-column width="70" prop="birthPer" label=""></el-table-column>
          <el-table-column width="70" prop="birthBase" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="" align="center">
          <el-table-column width="70" prop="accumulationFundPer" label=""></el-table-column>
          <el-table-column width="70" prop="accumulationFundBase" label=""></el-table-column>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button @click="showEditSalaryView(scope.row)"></el-button>
            <el-button type="danger" @click="deleteSalary(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        width="50%">
      <div style="display: flex;justify-content: space-around;align-items: center">
        <el-steps direction="vertical" :active="activeItemIndex">
          <el-step :title="itemName" v-for="(itemName,index) in salaryItemName" :key="index"></el-step>
        </el-steps>
        <el-input v-model="salary[title]" :placeholder="''+salaryItemName[index]+'...'"
                  v-for="(value,title,index) in salary"
                  :key="index" v-show="activeItemIndex==index" style="width: 200px"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="preStep">{{ activeItemIndex == 10 ? '' : '' }}</el-button>
    <el-button type="primary" @click="nextStep">{{ activeItemIndex == 10 ? '' : '' }}</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "SalSob",
  data() {
    return {
      dialogVisible: false,
      dialogTitle: '',
      salaries: [],
      activeItemIndex: 0,
      salaryItemName: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      salary: {
        basicSalary: 0,
        trafficSalary: 0,
        lunchSalary: 0,
        bonus: 0,
        pensionPer: 0,
        pensionBase: 0,
        medicalPer: 0,
        medicalBase: 0,
        accumulationFundPer: 0,
        accumulationFundBase: 0,
        name: ''
      }
    }
  },
  mounted() {
    this.initSalaries();
  },
  methods: {
    showEditSalaryView(data) {
      this.dialogTitle = '';
      this.dialogVisible = true;
      this.salary.basicSalary = data.basicSalary;
      this.salary.trafficSalary = data.trafficSalary;
      this.salary.lunchSalary = data.lunchSalary;
      this.salary.bonus = data.bonus;
      this.salary.pensionPer = data.pensionPer;
      this.salary.pensionBase = data.pensionBase;
      this.salary.medicalPer = data.medicalPer;
      this.salary.medicalBase = data.medicalBase;
      this.salary.accumulationFundPer = data.accumulationFundPer;
      this.salary.accumulationFundBase = data.accumulationFundBase;
      this.salary.name = data.name;
      this.salary.id = data.id;
      this.activeItemIndex = 0;
    },
    deleteSalary(data) {
      this.$confirm('' + data.name + '', '', {
        cancelButtonText: '',
        confirmButtonText: ''
      }).then(() => {
        this.deleteRequest("/salary/sob/" + data.id).then(resp => {
          if (resp) {
            this.initSalaries();
          }
        })
      }).catch(() => {
        this.$message.info("!");
      })
      this.activeItemIndex = 0;
    },
    preStep() {
      if (this.activeItemIndex == 0) {
        return;
      } else if (this.activeItemIndex == 10) {
        //
        this.dialogVisible = false;
        return;
      }
      this.activeItemIndex--;
    },
    nextStep() {
      if (this.activeItemIndex == 10) {
        if (this.salary.id) {
          this.putRequest("/salary/sob/", this.salary).then(resp => {
            if (resp) {
              this.initSalaries();
              this.dialogVisible = false;
            }
          })
        } else {
          this.postRequest("/salary/sob/", this.salary).then(resp => {
            if (resp) {
              this.initSalaries();
              this.dialogVisible = false;
            }
          });
        }
        this.activeItemIndex = 0;
        return;
      }
      this.activeItemIndex++;
    },
    showAddSalaryView() {
      //
      this.salary = {
        basicSalary: 0,
        trafficSalary: 0,
        lunchSalary: 0,
        bonus: 0,
        pensionPer: 0,
        pensionBase: 0,
        medicalPer: 0,
        medicalBase: 0,
        accumulationFundPer: 0,
        accumulationFundBase: 0,
        name: ''
      }
      this.dialogTitle = '';
      this.activeItemIndex = 0;
      this.dialogVisible = true;
    },
    getSob: function () {
      return new Promise((resolve, reject) => {
        this.getRequest("/salary/sob/").then((response) => {
          resolve(response);
        }).catch((error) => {
          reject(error);
        });
      })
    },
    async initSalaries() {
      var res = await this.getSob();
      this.salaries=res.obj;
    }
  }
}
</script>

<style scoped>

</style>